@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro);
.container-fluid{width:100%;max-width:1000px;margin:0 auto;padding:14px 28px}

.logo{font-weight:bold;font-size:28px;color:#333;margin:14px}

.main-menu{display:flex;flex-wrap:wrap;list-style-type:none;padding:0}.main-menu li{margin:0 14px}

.navbar{display:flex;justify-content:space-between;flex-wrap:wrap}

.page-title{margin:14px 0;font-size:56px;font-weight:600;position:relative;color:#333}

.button{background:transparent;border:1px solid;color:#666;text-align:center;line-height:1.3;padding:7px 14px;margin:14px 14px 14px 0;border-radius:5px;cursor:pointer;opacity:0.7;-webkit-transition:all 0.5s;transition:all 0.5s}.button--primary{color:#3498db}.button--info{color:#2b7cb3}.button--danger{color:#f0932b}.button:hover{box-shadow:-5px -5px 20px rgba(0,0,0,0.1);opacity:1}

.small-title{margin:7px 0;font-size:35px;font-weight:600;position:relative;color:#333}

.post-summary{border-bottom:1px solid #ddd;padding:14px;margin-bottom:14px;margin-left:-14px;-webkit-transition:.2s;transition:.2s;cursor:pointer}.post-summary:hover{margin-left:-28px;margin-top:-2.8px;border-radius:5px;box-shadow:0 0 20px rgba(0,0,0,0.1);padding-left:21px}

.lds-ellipsis{display:block;position:relative;width:64px;height:64px;margin:0 auto}.lds-ellipsis div{position:absolute;top:27px;width:11px;height:11px;border-radius:50%;background:#333;-webkit-animation-timing-function:cubic-bezier(0, 1, 1, 0);animation-timing-function:cubic-bezier(0, 1, 1, 0)}.lds-ellipsis div:nth-child(1){left:6px;-webkit-animation:lds-ellipsis1 0.6s infinite;animation:lds-ellipsis1 0.6s infinite}.lds-ellipsis div:nth-child(2){left:6px;-webkit-animation:lds-ellipsis2 0.6s infinite;animation:lds-ellipsis2 0.6s infinite}.lds-ellipsis div:nth-child(3){left:26px;-webkit-animation:lds-ellipsis2 0.6s infinite;animation:lds-ellipsis2 0.6s infinite}.lds-ellipsis div:nth-child(4){left:45px;-webkit-animation:lds-ellipsis3 0.6s infinite;animation:lds-ellipsis3 0.6s infinite}@-webkit-keyframes lds-ellipsis1{0%{-webkit-transform:scale(0);transform:scale(0)}100%{-webkit-transform:scale(1);transform:scale(1)}}@keyframes lds-ellipsis1{0%{-webkit-transform:scale(0);transform:scale(0)}100%{-webkit-transform:scale(1);transform:scale(1)}}@-webkit-keyframes lds-ellipsis3{0%{-webkit-transform:scale(1);transform:scale(1)}100%{-webkit-transform:scale(0);transform:scale(0)}}@keyframes lds-ellipsis3{0%{-webkit-transform:scale(1);transform:scale(1)}100%{-webkit-transform:scale(0);transform:scale(0)}}@-webkit-keyframes lds-ellipsis2{0%{-webkit-transform:translate(0, 0);transform:translate(0, 0)}100%{-webkit-transform:translate(19px, 0);transform:translate(19px, 0)}}@keyframes lds-ellipsis2{0%{-webkit-transform:translate(0, 0);transform:translate(0, 0)}100%{-webkit-transform:translate(19px, 0);transform:translate(19px, 0)}}

.alert{background:#fff;color:#333;border-left:4px solid #111;padding:0.9rem 0.8rem;margin:1rem 0 1.5rem 0;display:flex;align-items:center;font-size:1rem;opacity:1;font-weight:lighter;box-shadow:0 0 0 rgba(0,0,0,0.2);max-width:490px;border-radius:5px}.alert__desc{margin-left:10.76923px}.alert--error{background:#ff5d6c;color:#fff;border-color:#EA2027}.alert--warning{background:#d68529;color:#fff;border-color:#f0932b}.alert--info{background:#3598db;color:#fff;border-color:#2b7cb3}.alert--success{background:#00bc8c;color:#fff;border-color:#01916d}

.pagination{margin:0 auto;text-align:center}.pagination__list{display:flex;justify-content:center;list-style-type:none;font-size:21px}.pagination__list__item{display:block;width:28px;height:28px;margin:14px;display:flex;justify-content:center;align-items:center;cursor:pointer;border-radius:5px;-webkit-transition:.2s;transition:.2s;color:#666}.pagination__list__item:hover{color:#333}.pagination__list__item--active{color:#3498db}

.text-field{position:relative;display:block;margin:28px 0 14px 0}.text-field__input{border:none;border-bottom:1px solid #666;font-family:inherit;outline:none;font-size:inherit;padding:7px 0;-webkit-transition:.2s;transition:.2s;opacity:0.8;background:none;width:100%;max-width:280px}.text-field__input:hover{opacity:1}.text-field__input:focus{border-bottom:2px solid #3498db;opacity:1}.text-field__label{position:absolute;top:-14px;left:0;font-size:14px;-webkit-transition:.2s;transition:.2s}.text-field__label--big{font-size:inherit;top:4px}

.section-title{margin:14px 0;font-size:28px;font-weight:600;position:relative;color:#333}

.content-editor{outline:none;border:4px dashed #ddd;max-width:560px;padding:14px;font-family:"Source Sans Pro",sans-serif;-webkit-transition:0.2s;transition:0.2s}.content-editor:hover{border:4px dashed #ccc}

